jQuery = function(selectorOrArray){
    let elements
    if(typeof selectorOrArray === 'string'){
        elements = document.querySelectorAll(selectorOrArray)
    }else if(selectorOrArray instanceof Array){
        elements = selectorOrArray
    }
    return {
        addClass(className){
            for(let i=0;i<elements.length;i++){
                elements[i].classList.add(className)
            }
            return this  
        },
        oldApi:selectorOrArray.oldApi,
        find(selectorOrArray){
            let array = []
            for(let i=0;i<elements.length;i++){
                const elements2 = Array.from(elements[i].querySelectorAll(selectorOrArray))
                array = array.concat(elements2)
            }
            /*
            const newApi = jQuery(array)  //Ê≠§Êó∂ÁöÑ array ÊòØÊï∞ÁªÑÔºåËÄåÊúÄÂºÄÂßãÁöÑ jQuery ÂèÇÊï∞ÊòØÈÄâÊã©Âô®ÔºåÊâÄ‰ª•ÈúÄË¶ÅÂ∞ÜÂèÇÊï∞‰ªéselectorÊîπ‰∏∫selectorOrArrayÔºåÂÜçÂú®findËøôÈáåÊûÑÈÄ†‰∏Ä‰∏™Êñ∞ÁöÑjQueryÂØπË±°Ôºõ
            return newApi
            */
           array.oldApi = this  //Â∞ÜË∞ÉÁî®thisÁöÑÂØπË±°ÊîæÂú®array‰∏äÔºå‰ª•‰æøendÊñπÊ≥ïË∞ÉÁî®,‰ΩÜÊòØÊ≤°ÊúâÊîæÂú®apiË∫´‰∏äÔºåË∞ÉÁî®‰∏ç‰∫ÜÔºåÊâÄ‰ª•ÂÜçÂä†‰∏Ä‰∏™oldApiÊñπÊ≥ïÔºõ
           return jQuery(array)
        },
        end(){
            return this.oldApi  //Ê≠§Êó∂ÁöÑthisÊòØfindÊñπÊ≥ïË∞ÉÁî®ÂêéÊñ∞ÁîüÊàêÁöÑnewApiÂØπË±°Ôºõ
        },
        each(fn){
            for(let i=0;i<elements.length;i++){
                fn.call(null,elements[i],i)
            }
        },
        parent(){
            const array = []
            this.each((node)=>{
                if(array.indexOf(node.parentNode) === -1)
                  array.push(node.parentNode)
            })
            return jQuery(array)
        },
        print(){
            console.log(elements)
        },
        children(){
            const array = []
            this.each((node)=>{
                  array.push(...node.children)
                })
            return jQuery(array)
        },
        siblings(){
            let array = []
            this.each((node)=>{
                if(array.indexOf(node.parentNode.children) === -1)
                array.push(Array.from(node.parentNode.children).filter(n=>n!==node))
            })
            return jQuery(array)
        },
        index(){
            const list = elements[0].parentNode.children
            let i
            for(i=0;i<list.length;i++){
                if(list[i]===elements[0])
                break
            }
            console.log(i)
            return this
        },
        next(){
            let array = []
            let x = elements[0].nextSibling
            if(!x){
                return this
            }else if(x.nodeType===3){
                x = x.nextSibling
                array.push(x)
            }
            return jQuery(array)
        },
        prev(){
            let array = []
            let x = elements[0].previousSibling
            if(!x){
                return this
            }else if(x.nodeType===3){
                x = x.previousSibling
                array.push(x)
            }
            return jQuery(array)
        }
    }
}
